        margin-right: 50px;
        margin-left: 50px;
      }
    }
    .input-file-cours {

      width: 100%;
      height: 15%;
      display: flex;
      justify-content: center;
      align-items: center;

      .cours-submit {
        background-color: var(--red);
        border: none;
        border-radius: 15px 15px;
        cursor: pointer;
        height: 50%;
        width: 10%;
        margin-left: 50px;
      }

    }

    .name-cours, .auteur-cours{
      outline: none;
      padding-left: 20px;
